import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { NativeGeocoder, NativeGeocoderReverseResult, NativeGeocoderForwardResult, NativeGeocoderOptions } from '@ionic-native/native-geocoder';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public error : any = '';
  public error2 : any = '';
  public error3 : any = '';
  public locn : any = 'Unknown';
  public p : any = [];
  public mode : any = 0;
  public myname : any = '';

  constructor(public navCtrl: NavController, private geolocation: Geolocation, private nativeGeocoder: NativeGeocoder, private http: HttpClient) {
    this.p = { email : '', pass : '', empcode : ''};
    
  }

  ionViewDidLoad() {
     let that = this;
     let me = window.localStorage.getItem('me');
     if (!me) me='';
     if (me) {
       this.mode=1;
       this.myname = window.localStorage.getItem('myname');
     }
     else this.mode=0;
  }

  checkeml()
  {
         this.error='';
        let that = this;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checkeml.php?eml='+this.p.email+'&pwd='+this.p.pass).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                if (d.num) {
                  window.localStorage.setItem('myname',d.myname);
                  window.localStorage.setItem('me',d.num); 
                  this.mode=1; 
                  that.pinme();
                }
                else {
                  this.error='Incorrect - Please Try Again';
                }
        });


  }

  pinme()
  { 
this.geolocation.getCurrentPosition().then((resp) => {
   alert(JSON.stringify(resp.coords));
   let options: NativeGeocoderOptions = {
    useLocale: false,
    maxResults: 5
   };
   this.nativeGeocoder.reverseGeocode(resp.coords.latitude, resp.coords.longitude, options)
   .then((result: NativeGeocoderReverseResult[]) => alert(JSON.stringify(result[0])))
   .catch((error: any) => alert(error));

}).catch((error) => {
  alert('Error getting location' + JSON.stringify(error));
 let options: NativeGeocoderOptions = {
    useLocale: false,
    maxResults: 5
   };
this.nativeGeocoder.reverseGeocode(52.5072095, 13.1452818, options)
   .then((result: NativeGeocoderReverseResult[]) => alert(JSON.stringify(result[0])))
   .catch((error: any) => alert(error));
});
 }

 logout()
 { window.localStorage.removeItem('me');
   window.localStorage.setItem('myname','');
   this.mode=0;
 }
 

}



